import { customRef } from 'vue'

export default function useDelayRef(value) {
    return customRef((track, trigger) => {
        let timer;
        return {
            get() {
                // console.log(`ä»myRefè¿™ä¸ªå®¹å™¨è¯»å–æ•°æ®,data:${value}`);
                track(); //é€šçŸ¥è¿½è¸ªvalueçš„å˜åŒ–(è·Ÿgetterå•†é‡ä¸€ä¸‹è®©å®ƒæ˜ç¡®ä½ è¿™ä¸ªvalueæ˜¯æœ‰ç”¨çš„)
                return value; //è¯»å–çš„æ—¶å€™å°±ä¼šè°ƒç”¨get
            },
            set(nv) {
                // console.log(`myRefå®¹å™¨ä¸­çš„æ•°æ®è¢«ä¿®æ”¹,dataæ”¹ä¸º${nv}`);
                clearTimeout(timer);
                timer = setTimeout(() => {
                    value = nv;
                    trigger(); //triggeré€šçŸ¥vueé‡æ–°è§£ææ¨¡ç‰ˆ //é˜²æŠ–
                }, 500);
            }
        }
    })
}